@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html, body {
  width: 100%;
  min-height: 100vh; }

html {
  font-size: 62.5%; }
  @media screen and (max-width: 1166px) {
    html {
      font-size: 56%; } }
  @media screen and (max-width: 500px) {
    html {
      font-size: 52.5%; } }

body {
  font-family: 'Lato', sans-serif;
  font-size: 1.6rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: inherit;
  background-image: linear-gradient(105deg, #1d1d1d, #3a3a3a);
  background-size: cover;
  background-repeat: no-repeat; }

#root {
  width: 100%; }

#App {
  position: relative; }

#searchbar {
  width: 100%;
  height: 4rem;
  padding: .5rem .8rem;
  font-size: 1.6rem; }
  #searchbar::-webkit-input-placeholder {
    text-transform: uppercase; }

#customer-list {
  width: 65rem;
  margin-left: 5rem;
  margin-top: 5rem;
  height: 100%;
  box-shadow: 0 .5rem 1.5rem #161616f8; }
  @media screen and (max-width: 966px) {
    #customer-list {
      width: 90%;
      margin-top: 4rem;
      margin-left: 0; } }
  @media screen and (max-width: 500px) {
    #customer-list {
      width: 100%;
      margin-top: 0; } }

.customers-table {
  width: 100%;
  height: 200px;
  border: 2px solid #bbbbbbd8; }
  .customers-table .fixed-table-head {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: #219fa3;
    padding: .5rem;
    color: #eeecec; }
    .customers-table .fixed-table-head tr {
      border: 1px solid red; }
  .customers-table .scroll-table {
    display: block;
    overflow: auto;
    height: 229px;
    width: 100%;
    background-color: #eeecec; }
    .customers-table .scroll-table tr {
      display: -webkit-flex;
      display: flex; }
      .customers-table .scroll-table tr .value-field {
        border: 1px darkviolet;
        text-align: right; }
      .customers-table .scroll-table tr td {
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding: .5rem 1rem;
        -webkit-flex: 1 1;
                flex: 1 1; }
      .customers-table .scroll-table tr:nth-child(2n) {
        background-color: #dbd8d8; }
  .customers-table .noUsers {
    height: 200px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    color: #d60f0f;
    text-transform: uppercase; }
    .customers-table .noUsers span {
      margin: auto; }

footer {
  background-color: #161616;
  width: 100%;
  color: #eee;
  text-align: center;
  padding: 2.5rem 1rem; }

#header {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 2rem 2.5rem;
  background-color: #fff;
  border-bottom: 2px solid rgba(141, 140, 140, 0.493);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10; }
  @media screen and (max-width: 650px) {
    #header {
      -webkit-flex-direction: column-reverse;
              flex-direction: column-reverse;
      -webkit-align-items: center;
              align-items: center; } }

@media screen and (max-width: 650px) {
  #company-logo {
    margin-top: 1rem;
    width: 150px; } }

@media screen and (max-width: 500px) {
  #company-logo {
    width: 125px; } }

@media screen and (max-width: 400px) {
  #company-logo {
    width: 100px; } }

#title {
  font-weight: 400;
  text-transform: uppercase;
  color: #2b2b2b;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center; }
  @media screen and (max-width: 650px) {
    #title {
      font-size: 2.8rem; } }
  @media screen and (max-width: 500px) {
    #title {
      font-size: 2.3rem; } }
  @media screen and (max-width: 400px) {
    #title {
      font-size: 2rem; } }

.field {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  width: 90%;
  margin: 5rem auto; }
  @media screen and (max-width: 966px) {
    .field {
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center; } }
  @media screen and (max-width: 500px) {
    .field {
      width: 100%; } }

.svg-container {
  display: inline-block;
  position: relative;
  width: 100%;
  padding-bottom: 80%;
  /* aspect ratio */
  vertical-align: top;
  overflow: hidden; }
  .svg-container rect {
    fill: #26d5db; }
  .svg-container text {
    fill: #e61d1d; }
  .svg-container g {
    font-size: 2rem; }

.svg-responsive {
  display: inline-block;
  position: absolute;
  top: 10px;
  left: 0;
  padding-left: 2.5rem; }

.tables {
  display: -webkit-flex;
  display: flex;
  width: 90%;
  margin: 15rem auto 3rem; }
  @media screen and (max-width: 999px) {
    .tables {
      -webkit-flex-direction: column;
              flex-direction: column; } }
  @media screen and (max-width: 500px) {
    .tables {
      width: 100%;
      margin: 15rem 0 3rem; } }

.sales-table {
  border: 1px solid #ccc;
  border-collapse: collapse;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 30rem;
  table-layout: fixed;
  box-shadow: 0 .5rem 1.5rem #161616f8; }
  @media screen and (max-width: 650px) {
    .sales-table {
      border: 0; } }
  .sales-table:not(:last-child) {
    margin-right: 1.5rem; }
    @media screen and (max-width: 999px) {
      .sales-table:not(:last-child) {
        margin-right: 0;
        margin-bottom: 4rem; } }
  @media screen and (max-width: 650px) {
    .sales-table tr {
      border-bottom: 3px solid #ddd;
      display: block;
      margin-bottom: .625em; } }
  @media screen and (max-width: 650px) {
    .sales-table td {
      border-bottom: 1px solid #ddd;
      display: -webkit-flex;
      display: flex;
      -webkit-justify-content: space-between;
              justify-content: space-between;
      font-size: .8em; } }
  @media screen and (max-width: 650px) {
    .sales-table td::before {
      content: attr(datalabel);
      display: block;
      -webkit-justify-content: flex-start;
              justify-content: flex-start;
      font-weight: bold;
      text-transform: uppercase; } }
  .sales-table th, .sales-table td {
    padding: .625em;
    text-align: center; }
  .sales-table th {
    font-size: .85em;
    letter-spacing: .1em;
    text-transform: uppercase; }
  .sales-table__caption {
    font-size: 1.3em;
    font-weight: 300;
    color: #eee;
    margin: .5em 0 .75em;
    text-transform: uppercase; }
  .sales-table__row {
    background-color: #f8f8f8;
    border: 1px solid #ddd;
    padding: .35em; }
  .sales-table__head tr {
    background-color: #219fa3;
    color: #eeecec; }
  @media screen and (max-width: 650px) {
    .sales-table__head {
      border: none;
      clip: rect(0 0 0 0);
      height: 1px;
      margin: -1px;
      overflow: hidden;
      padding: 0;
      position: absolute;
      width: 1px; } }

.svg-container {
  max-width: 100%; }

